.card {
  background: #f9f9f9;
  border: 1px solid rgb(241, 197, 197);
  padding: 7px;
  text-align: center;
  border-radius: 30px;
}

.card__img {
  width: 20%;
}

.concave {
  border-radius: 5px;
  margin: 0;
  box-sizing: border-box;
  border: none;
  outline:none;
  height: 40px;
  cursor: pointer;
  background: #ffffff;
  box-shadow: inset 3px 3px 8px rgba(46, 53, 62, 0.9),
  1px 1px 2px rgba(66, 75, 88, 0.3), -1px -1px 2px 
  rgba(46, 53, 62, 0.5), inset -3px 3px 6px rgba(46, 53, 62, 0.2), 
  inset 3px -3px 6px rgba(46, 53, 62, 0.2), inset -3px -3px 6px 
  rgba(66, 75, 88, 0.9);
  transition: all 1s linear;
}

.concave:hover {
  background: #ffffff;
  box-shadow: inset -5px 5px 10px rgba(48, 54, 64, 0.2), 5px -5px 10px 
  rgba(48, 54, 64, 0.2), -5px -5px 10px rgba(64, 74, 86, 0.9), 5px 5px 13px 
  rgba(48, 54, 64, 0.9), inset 1px 1px 2px rgba(64, 74, 86, 0.3), 
  inset -1px -1px 2px rgba(48, 54, 64, 0.5);
}
